
.h-container {
  margin: 0 4%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.h-tagline {
  padding: 0 0 0 3%;
}

.h-nav-btns-container {
  padding: 40px 0 0 0;
  display: flex;
  justify-content: flex-end;
}

.h-nav-btn {
  margin: 0 0 0 30px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  padding-bottom: 20px;
  border-bottom: 3px solid transparent;
  transition: 0.9s;
  color: #fff;
}

.h-nav-btn:hover {
  border-bottom: 3px solid rgba(234, 202, 187);
  cursor: pointer;
}

.h-main-icon {
  margin-top: 10px;
  height: 80px;
  width: 80px;
}

.h-main-icon:hover {
  margin-top: 10px;
  height: 80px;
  width: 80px;
}
.f-container {
  background-color: #EACABB;
  margin: 0;
  height: 150px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.f-social-icon {
  height: 25px;
  width: 25px;
  margin: 0 10px;
}
.i-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -96px;
  height: 100vh;
  background-image: url(/static/media/bananatree.c3537c6b.jpeg);
  background-size: cover;
}

.i-title {
  padding: 300px 0 0;
  margin: 0;
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: #fff;
  font-weight: normal;
}

.i-break {
  width: 10%;
  border-bottom: 3px solid rgba(234, 202, 187);
  height: 30px;
}

.i-tagline {
  letter-spacing: 4px;
  padding: 0 0 200px;
  color: #fff;
  font-weight: normal;
}
#about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 15%;
  border-top: 15px solid rgba(234, 202, 187);
}

.am-content-container {
  display: flex;
  align-items: center;
  padding-bottom: 150px;
}

.am-text {
  text-align: left;
  padding: 10px 0;
  margin: 0;
}

.am-me-icon {
  height: 30%;
  width: 30%;
  border-radius: 50%;
  margin-right: 75px;
}

.am-break {
  width: 400px;
  border-bottom: 1px solid rgba(234, 202, 187, .8);
}
.pp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pp-projects-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 50px 0;
}

.pp-project-container {
  height: 300px;
  width: 45%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1%;
}

.pp-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: normal;
}

.pp-icon {
  width: 40px;
  height: 40px;
  margin: 40px;
}

.pp-pantone {
    background: 
    linear-gradient(
      rgba(234, 202, 187, .3),
      rgba(234, 202, 187, .3)),
    url(/static/media/pantone-landing.3bbcb14b.png);
  background-size: cover;
  width: 92%;
}

.pp-pantone:hover {
  background-image: url(/static/media/pantone-landing.3bbcb14b.png);
  cursor: pointer;
}

.pp-nordstrom-notes {
    background: 
    linear-gradient(
      rgba(234, 202, 187, .3),
      rgba(234, 202, 187, .3)),
    url(/static/media/nordstrom-notes-landing.4e583504.png);
  background-size: cover;
}

.pp-nordstrom-notes:hover {
  background-image: url(/static/media/nordstrom-notes-landing.4e583504.png);
  cursor: pointer;
}

.pp-jetpack {
    background: 
    linear-gradient(
      rgba(234, 202, 187, .3),
      rgba(234, 202, 187, .3)),
    url(/static/media/jetpack-landing.c1c3ee1f.png);
  background-size: cover;
}

.pp-jetpack:hover {
  background-image: url(/static/media/jetpack-landing.c1c3ee1f.png);
  cursor: pointer;
}

.pp-mentor {
    background: 
    linear-gradient(
      rgba(234, 202, 187, .3),
      rgba(234, 202, 187, .3)),
    url(/static/media/mentor-landing.71943282.png);
  background-size: cover;
  background-position: -200px
}

.pp-mentor:hover {
  background-image: url(/static/media/mentor-landing.71943282.png);
  cursor: pointer;
}

.pp-harvard {
    background: 
    linear-gradient(
      rgba(234, 202, 187, .3),
      rgba(234, 202, 187, .3)),
    url(/static/media/harvard-landing.1dff75c9.png);
}

.pp-harvard:hover {
  background-image: url(/static/media/harvard-landing.1dff75c9.png);
  cursor: pointer;
}
.p-container {
  display: flex;
  flex-direction: column;
  width: 60%;
  height: 700px;
}

.p-title {
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 40px;
}

.p-project-img {
  width: 500px;
  margin: 0 auto;
}

.p-tech-icon {
  height: 30px;
  padding-right: 20px;
}

.p-btns {
  display: flex;
}

.p-btn {
  background: rgba(222, 172, 148, .7);
  color: #FFF;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: .6rem;
  margin: 2px;
  transition: .5s;
  width: 33%;
  height: 20px;
}

.p-btn:hover {
  background: rgba(222, 172, 148);
}
* {
  font-family: 'Raleway', sans-serif;
  color: #5f5f5f;
  box-sizing: border-box;
}

body {
  line-height: 1.4;
  letter-spacing: 1px;
  margin: 0;
}
