.p-container {
  display: flex;
  flex-direction: column;
  width: 60%;
  height: 700px;
}

.p-title {
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 40px;
}

.p-project-img {
  width: 500px;
  margin: 0 auto;
}

.p-tech-icon {
  height: 30px;
  padding-right: 20px;
}

.p-btns {
  display: flex;
}

.p-btn {
  background: rgba(222, 172, 148, .7);
  color: #FFF;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: .6rem;
  margin: 2px;
  transition: .5s;
  width: 33%;
  height: 20px;
}

.p-btn:hover {
  background: rgba(222, 172, 148);
}