.pp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pp-projects-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 50px 0;
}

.pp-project-container {
  height: 300px;
  width: 45%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1%;
}

.pp-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: normal;
}

.pp-icon {
  width: 40px;
  height: 40px;
  margin: 40px;
}

.pp-pantone {
    background: 
    linear-gradient(
      rgba(234, 202, 187, .3),
      rgba(234, 202, 187, .3)),
    url(../utils/assets/pantone-landing.png);
  background-size: cover;
  width: 92%;
}

.pp-pantone:hover {
  background-image: url(../utils/assets/pantone-landing.png);
  cursor: pointer;
}

